
<template>
      <div class="section">
        <div class="main">
          <p class="section-label">Message </p>
          <v-button type="primary" size="small" @click="showMessage"></v-button>
          <v-button type="primary" size="small" @click="showSuccessMessage"></v-button>
          <v-button type="primary" size="small" @click="showErrorMessage"></v-button>
          <v-button type="primary" size="small" @click="showWarnMessage"></v-button>
          <v-button type="primary" size="small" @click="showLoadingMessage">...</v-button>
        </div>
      </div>
</template>

<script>
    import VButton from "../../common/button"
    
    export default {
        components: {
            VButton
        },
        methods: {
            showMessage() {
        this.$vmessage.info({
          text: ""
        });
      },
      showSuccessMessage() {
        this.$vmessage.success({
          text: ""
        });
      },
      showErrorMessage() {
        this.$vmessage.error({
          text: ""
        });
      },
      showWarnMessage() {
        this.$vmessage.warning({
          text: ""
        });
      },
      showLoadingMessage() {
        this.$vmessage.loading({
          text: "",
          duration: 1000000000
        });
      }
        }
    }
</script>

<style lang="scss" scoped>

        .main {
            margin: 0 20px;
        }
        .section-label {
            color: #657180;
            font-weight: bold;
            font-size: 18px;
            margin: 0 0 10px 0;
        }
</style>





